.products {
	padding: 50px 0;
	background-color: #f7f7f7;
}

.products__title {
	margin-bottom: 32px;
	text-align: center;
}

.products__tabs {
	margin-bottom: 80px;
	text-align: center;
}

.products__slider {
	margin-bottom: clamp(30px, 0.982rem + 4.46vw, 60px);

	@include desktop-small() {
		padding: 0 40px;
	}

	@include mobile() {
		padding: 0 20px;
	}
}

.products__link {
	text-align: center;
	font-weight: 400;
	font-size: 18px;
	line-height: 185%;
}
